<template>
  <div class="home_container">
    <!-- 搜索区 -->
    <van-nav-bar class="topbar-container">
      <van-button to="/search" slot="title" round icon="search" size="small">
        搜索
      </van-button>
    </van-nav-bar>
    <!-- 导航条 -->

    <van-tabs class="channel_navbar" v-model="active" sticky swipeable>
      <van-tab v-for="item in channelList" :key="item.id" :title="item.name">
        <article-list :channel="item" />
      </van-tab>
      <div slot="nav-right" class="placehoder_item"></div>

      <div slot="nav-right" class="nav_popup" @click="isPopupShow = true">
        <van-icon name="wap-nav" />
      </div>
    </van-tabs>

    <!-- 弹出层 -->
    <van-popup
      class="popup"
      :close-on-popstate="true"
      :closeable="true"
      close-icon-position="top-right"
      v-model="isPopupShow"
      position="left"
      get-container="body"
    >
      <channel-edit
        :activeIndex="active"
        @changeChannel="checkUserChennel"
        @deleteChannel="active = $event"
        :channelList="channelList"
      ></channel-edit>
    </van-popup>
  </div>
</template>

<script>
import { getUserChannels } from '@/api/home.js';
import { mapState } from 'vuex';
import { getItem, setItem } from '@/utils/storage.js';

export default {
  name: 'Home',
  components: {
    articleList: () => import('./components/article.vue'),
    channelEdit: () => import('./components/channel.vue'),
  },
  data() {
    return {
      active: this.$route.params.active || 0, // 默认导航索引
      channelList: [], // 导航列表
      isPopupShow: false, // 是否显示弹出层
    };
  },
  computed: {
    ...mapState(['userToken']),
  },
  methods: {
    // 获取用户频道
    async getChannels() {
      let channels = [];
      // 如果已登录
      if (this.userToken) {
        try {
          const { data } = await getUserChannels();
          channels = data.data.channels;
        } catch (e) {
          console.log(e);
        }
      } // 如果未登录
      else {
        const localChannels = getItem('user_chennels');
        // 未登录 但是有本地频道
        if (localChannels) {
          channels = localChannels;
        } else {
          // 未登录 且没有本地频道
          try {
            const { data } = await getUserChannels();
            channels = data.data.channels;
          } catch (e) {
            console.log(e);
          }
        }
      }
      this.channelList = channels;
    },
    // 切换频道
    checkUserChennel(ind) {
      this.isPopupShow = false;
      this.active = ind;
    },
  },
  created() {
    this.getChannels();
  },
  mounted() {},
  beforeRouteLeave(to, from, next) {
    setItem('active', this.active);
    next();
  },
};
</script>

<style lang="less" scoped>
.home_container {
  /deep/.channel_navbar {
    .nav_popup {
      position: fixed;
      z-index: 10;
      right: 0;
      width: 33px;
      line-height: 40px;
      text-align: center;
      font-size: 22px;
      background-color: rgba(255, 255, 255, 0.9);
    }
    .placehoder_item {
      flex: 1 0 33px;
    }
    .van-tab {
      color: #3a3a3a;
    }
    .van-tab--active {
      font-weight: 700;
      color: #3296fa;
    }
    .van-tabs__line {
      width: 15px;
      height: 3px;
      background-color: #3296fa;
    }
  }

  /deep/.van-nav-bar__title {
    max-width: 100%;
    .van-button {
      background-color: #5babfb;
      border: none;
      color: #fff;
      width: 278px;
      .van-button__text {
        font-size: 14px;
      }
      .van-icon-search {
        font-size: 18px;
      }
    }
  }
}
.popup {
  width: 100%;
  height: 100%;
}
</style>
